import React from 'react'
import { Link as GatsbyLink } from 'gatsby'
import MuiLink from '@material-ui/core/Link'
import Tab from '@material-ui/core/Tab'

/**
 * Material ui Links for Gatsby
 * @type {React.ForwardRefExoticComponent<React.PropsWithoutRef<{}> & React.RefAttributes<unknown>>}
 */
const Link = React.forwardRef((props, ref) => {
  return <MuiLink component={GatsbyLink} ref={ref} underline='none' {...props} />
})

/**
 * Custom Links to be used in Tabs
 * @type {React.ForwardRefExoticComponent<React.PropsWithoutRef<{}> & React.RefAttributes<unknown>>}
 */
export const TabLink = React.forwardRef((props, ref) => {
  return <Tab component={GatsbyLink} ref={ref} {...props} />
})

export default Link
